import React, { useState } from "react";
import { createClient } from "@supabase/supabase-js";
import { Navbar, Nav } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import "./index.css";

const supabaseUrl = "https://islsyvscfmsxebmsrhok.supabase.co";
const supabaseKey = "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImlzbHN5dnNjZm1zeGVibXNyaG9rIiwicm9sZSI6ImFub24iLCJpYXQiOjE3NDI5MDEwNjgsImV4cCI6MjA1ODQ3NzA2OH0.J5_2Seqk3zbQOm8TTk2nvnSVFBcSyTqU1757qMTA_U4";
const supabase = createClient(supabaseUrl, supabaseKey);

export default function App() {
  const [name, setName] = useState(""); // 
  const [account, setAccount] = useState(""); //  (text)
  const [password, setPassword] = useState(""); // 
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
  const [activePage, setActivePage] = useState("login");
  const [loggedInUser, setLoggedInUser] = useState(null);

  // 
  const handleRegister = async () => {
    setError("");
    setSuccess("");

    const { data, error: insertError } = await supabase
      .from("users")
      .insert([{ name, account, password }]);

    if (insertError) {
      setError(`: ${insertError.message}`);
    } else {
      setSuccess("");
    }
  };

  // 
  const handleLogin = async () => {
    setError("");
    setSuccess("");

    const { data, error } = await supabase
      .from("users")
      .select("*")
      .eq("account", account)
      .eq("password", password)
      .single();

    if (error) {
      setError("");
    } else {
      setLoggedInUser(data); // 
      setSuccess("");
      setActivePage("profile"); // 
    }
  };

  // 
  const handleLogout = async () => {
    setLoggedInUser(null);
    setAccount("");
    setPassword("");
    setActivePage("login"); // 
  };

  // 
  const handleUpdateProfile = async () => {
    setError("");
    setSuccess("");
  
    const { data, error: updateError } = await supabase
      .from("users")
      .update({ name, account, password })
      .eq("id", loggedInUser.id)
      .select(); // 
  
    if (updateError) {
      setError(`: ${updateError.message}`);
    } else {
      if (data && data.length > 0) {
        setLoggedInUser(data[0]); // 
        setSuccess("");
      } else {
        setError("");
      }
    }
  };
  

  return (
    <div>
      <Navbar bg="dark" variant="dark" expand="lg">
        <Navbar.Brand href="#"></Navbar.Brand>
        <Nav className="ml-auto">
          <Nav.Link onClick={() => setActivePage("register")}></Nav.Link>
          <Nav.Link onClick={() => setActivePage("login")}></Nav.Link>
        </Nav>
      </Navbar>

      {activePage === "register" && (
        <div className="register-container">
          <h2></h2>
          {error && <p className="error-message">{error}</p>}
          {success && <p className="success-message">{success}</p>}
          <input
            type="text"
            placeholder=""
            className="input-field"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
          <input
            type="text"
            placeholder=""
            className="input-field"
            value={account}
            onChange={(e) => setAccount(e.target.value)}
          />
          <input
            type="password"
            placeholder=""
            className="input-field"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <button className="register-button" onClick={handleRegister}></button>
        </div>
      )}

      {activePage === "login" && (
        <div className="login-container">
          <h2></h2>
          {error && <p className="error-message">{error}</p>}
          {success && <p className="success-message">{success}</p>}
          <input
            type="text"
            placeholder=""
            className="input-field"
            value={account}
            onChange={(e) => setAccount(e.target.value)}
          />
          <input
            type="password"
            placeholder=""
            className="input-field"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <button className="login-button" onClick={handleLogin}></button>
        </div>
      )}

      {activePage === "profile" && loggedInUser && (
        <div className="profile-container">
          <h2></h2>
          {error && <p className="error-message">{error}</p>}
          {success && <p className="success-message">{success}</p>}
          <input
            type="text"
            placeholder=""
            className="input-field"
            value={name || loggedInUser.name}
            onChange={(e) => setName(e.target.value)}
          />
          <input
            type="text"
            placeholder=""
            className="input-field"
            value={account || loggedInUser.account}
            onChange={(e) => setAccount(e.target.value)}
          />
          <input
            type="password"
            placeholder=""
            className="input-field"
            value={password || loggedInUser.password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <button className="update-button" onClick={handleUpdateProfile}></button>
          <button className="logout-button" onClick={handleLogout}></button>
        </div>
      )}
    </div>
  );
}
